<template>
  <div
    @mouseenter="onMouseenter"
    @mouseleave="onMouseleave"
    class="popover-container"
  >
    <!-- 插槽一：具名，触发弹层的视图 -->
    <div ref="referenceRef">
      <slot name="reference"></slot>
    </div>
    <!-- 插槽二：匿名，弹出层展示的内容 -->
    <transition name="slide">
      <div
        v-show="isVisible"
        class="popover-dropdown"
        ref="dropdownRef"
        :style="dropdownStyle"
      >
        <slot />
      </div>
    </transition>
  </div>
</template>

<script>
import { placeEnum } from '@/constants/index' // 此处得解构否则拿不到
export default {
  data() {
    return {
      isVisible: false,
      // 控制 dropdown 位置
      dropdownStyle: {
        top: 0,
        left: 0
      },
      timeout: null,
      delay: 100
    }
  },
  props: {
    placement: {
      type: String,
      default: 'bottom-left',
      validator(val) {
        const is = placeEnum.includes(val)
        if (!is) {
          throw new Error(
            `你的 placement 必须是 ${placeEnum.join('、')} 中的一个`
          )
        }
        return is
      }
    }
  },
  methods: {
    // 防抖：
    onMouseenter() {
      this.isVisible = true
      if (this.timeout) {
        clearTimeout(this.timeout)
      }
    },
    onMouseleave() {
      this.timeout = setTimeout(() => {
        this.isVisible = false
        this.timeout = null
      }, this.delay)
    },
    // 计算元素的长宽
    getElmSize(target) {
      if (!target) return
      return {
        width: target.offsetWidth,
        height: target.offsetHeight
      }
    }
  },
  watch: {
    isVisible: function (val) {
      if (!val) return
      this.$nextTick(() => {
        var referenceWidth = this.getElmSize(this.$refs.referenceRef).width
        var referenceHeight = this.getElmSize(this.$refs.referenceRef).height
        var dropdownWidth = this.getElmSize(this.$refs.dropdownRef).width
        switch (this.placement) {
          case 'top-left':
            this.dropdownStyle.top = 0
            this.dropdownStyle.left = -dropdownWidth + 'px'
            break
          case 'top-right':
            this.dropdownStyle.top = 0
            this.dropdownStyle.left = referenceWidth + 'px'
            break
          case 'bottom-left':
            this.dropdownStyle.top = referenceHeight + 'px'
            this.dropdownStyle.left = -dropdownWidth + 'px'
            break
          case 'bottom-right':
            this.dropdownStyle.top = referenceHeight + 'px'
            this.dropdownStyle.left = referenceWidth + 'px'
            break
          default:
            break
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.popover-container {
  position: relative;
}
.popover-dropdown {
  position: absolute;
  background-color: white;
  border-width: 1px;
  border-radius: 0.375rem;
  z-index: 20;
}
// slide 展示动画
.slide-enter-active {
  transition: opacity 0.3s, transform 0.3s;
}

.slide-leave-active {
  transition: opacity 0.3s, transform 0.3s;
}

.slide-enter,
.slide-leave-to {
  transform: translateY(20px);
  opacity: 0;
}
</style>
