<template>
  <svg aria-hidden="true">
    <use :xlink:href="getIconName" :class="fillClass" :fill="color" />
  </svg>
</template>

<script>
export default {
  name: 'svg-icon',
  props: {
    // svg 名称
    name: {
      type: String,
      required: true
    },
    // svg 颜色
    color: {
      type: String
    },
    // 样式相关
    fillClass: {
      type: String
    }
  },
  computed: {
    getIconName() {
      return `#icon-${this.name}`
    }
  }
}
</script>

<style lang="scss" scoped></style>
