<template>
  <button
    class="button"
    :class="[typeEnum[type], sizeEnum[sizeKey].button]"
    @click.stop="onBtnClick"
  >
    <!-- loading-->
    <base-svg-icon
      v-if="isLoading"
      name="loading"
      class="loading"
    ></base-svg-icon>
    <!-- icon 按钮 -->
    <base-svg-icon
      v-if="icon"
      class="icon"
      :name="icon"
      :class="sizeEnum[sizeKey].icon"
      :color="iconColor"
    ></base-svg-icon>
    <!-- 文字按钮 -->
    <slot v-else />
  </button>
</template>

<script>
import { typeEnum, sizeEnum } from '@/constants/index'
export default {
  data() {
    return {
      typeEnum: {
        primary: 'primary',
        main: 'main',
        info: 'info'
      },
      sizeEnum: {
        default: { button: 'default-button', icon: '' },
        icon_default: { button: 'icon-default-button', icon: 'icon-default' },
        small: { button: 'small-button', icon: '' },
        icon_small: { button: 'icon-small-button', icon: 'icon_small' }
      }
    }
  },
  props: {
    // icon 图标名
    icon: {
      type: String
    },
    // icon 颜色
    iconColor: {
      type: String
    },
    // icon 类名
    iconClass: {
      type: String
    },
    // 按钮风格
    type: {
      type: String,
      default: 'main',
      // 判断 type 传入值是否在可选数组中，否则抛出错误
      validator(val) {
        // 获取所有 type 选项
        const keys = Object.keys(typeEnum)
        // 判断当前 props 传的值是否在 type 选项数组中
        const res = keys.includes(val)
        // 不在抛出错误
        if (!res) {
          throw new Error(`type 必须是 ${keys.join('、')} 中的一个`)
        }
        return res
      }
    },
    // 大小风格
    size: {
      type: String,
      default: 'default',
      // 过滤掉 icon 前缀，即 props 传入时不带 icon 前缀，但是后续使用到 icon 图标时需要重新加上 icon-前缀
      validator(val) {
        const keys = Object.keys(sizeEnum).filter((key) => {
          if (!key.includes('icon')) {
            return key
          }
        })
        const res = keys.includes(val)
        if (!res) {
          throw new Error(`size 必须是 ${keys.join('、')} 中的一个`)
        }
        return res
      }
    },
    // 是否点击动画
    isActiveAnimation: {
      type: Boolean,
      default: true
    },
    // 是否 loading
    isLoading: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    onBtnClick() {
      if (this.isLoading) {
        return
      }
      // console.log('emit send click')
      this.$emit('click')
    }
  },
  computed: {
    // 给 icon size 加上前缀
    sizeKey: function () {
      return this.icon ? 'icon_' + this.size : this.size
    }
  }
}
</script>

