  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.25rem;
}
.loading {
  width: 0.5rem;
  height: 0.5rem;
  // animation: 1s linear infinite;
}
.icon {
  margin: auto;
}

// type 相关样式
.primary {
  background-color: blue;
}
.main {
  background-color: #d6381d;
}
.info {
  background-color: green;
}

// size 相关样式
.default-button {
  width: 5rem;
  height: 2.5rem;
  font-size: 1rem;
  line-height: 0.52rem;
}
.icon-default-button {
  width: 3rem;
  height: 3rem;
}
.icon-default {
  width: 2rem;
