}
.small-button {
  width: 2.5rem;
  height: 1.25rem;
  font-size: 0.42rem;
  line-height: 0.52rem;
}
.icon-small-button {
  width: 2rem;
  height: 2rem;
}
.icon_small {
  width: 1.5rem;
  height: 1.5rem;
}
</style>
